.container {
  .itemContainer {
    
    max-width: 700px;
    margin: auto;
    margin-bottom: 100px;
  }

  h3 {
    font-style: normal;
    font-weight: 900;
    font-size: 36px;
    line-height: 44px;

    color: #45B5AA;

    text-shadow: 2px 2px 0px #E0E0E0;
  }

  p {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;

    color: #454545;
    white-space: pre-wrap;
  }
}
